import React, { useState, useEffect } from "react";
import { getEngagement } from "../api";
import { Line } from "react-chartjs-2";

function EngagementChart() {
  const [records, setRecords] = useState([]);
  const [alertMsg, setAlertMsg] = useState("");

  const loadData = async () => {
    try {
      const data = await getEngagement();
      setRecords(data);
      if (data.length > 0) {
        // check the latest record
        const last = data[0];
        if (last.phone_detected || last.gaze !== "focused") {
          setAlertMsg(
            `ALERT: Student not focusing or using phone. phone=${last.phone_detected}, gaze=${last.gaze}`
          );
        } else {
          setAlertMsg("");
        }
      }
    } catch (err) {
      console.error(err);
    }
  };

  useEffect(() => {
    loadData();
    const interval = setInterval(loadData, 60_000); // poll every min
    return () => clearInterval(interval);
  }, []);

  const chartData = {
    labels: records.map(r => r.timestamp.slice(11, 19)),
    datasets: [
      {
        label: "Face Count",
        data: records.map(r => r.face_count),
        borderWidth: 2
      }
    ]
  };

  return (
    <div style={{ marginTop: "20px" }}>
      <h2>Engagement Chart</h2>
      {alertMsg && <p style={{ color: "red", fontWeight: "bold" }}>{alertMsg}</p>}
      <Line data={chartData} />
      <table border="1" cellPadding="5" style={{ marginTop: "10px" }}>
        <thead>
          <tr>
            <th>ID</th>
            <th>Timestamp</th>
            <th>Faces</th>
            <th>Student</th>
            <th>Phone?</th>
            <th>Gaze</th>
            <th>Head Pos</th>
            <th>Image</th>
          </tr>
        </thead>
        <tbody>
          {records.map(r => (
            <tr key={r.id}>
              <td>{r.id}</td>
              <td>{r.timestamp}</td>
              <td>{r.face_count}</td>
              <td>{r.recognized_student || "Unknown"}</td>
              <td>{r.phone_detected ? "Yes" : "No"}</td>
              <td>{r.gaze}</td>
              <td>{r.head_position}</td>
              <td>
                {r.blob_url ? (
                  <img src={r.blob_url} alt="capture" width="80" />
                ) : null}
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}

export default EngagementChart;
