import React, { useState, useEffect, useRef } from "react";
import { enrollStudent } from "../api";

/**
 * A single component that:
 * 1) Tries to get camera access via getUserMedia.
 * 2) Provides a file upload fallback.
 * 3) Lets the user enter a student name.
 * 4) Uploads the final chosen image to your backend to enroll a new student.
 */
function Enrollment() {
  const [studentName, setStudentName] = useState("");
  const [hasCamera, setHasCamera] = useState(false);
  const [cameraError, setCameraError] = useState("");
  const [stream, setStream] = useState(null);

  const [previewUrl, setPreviewUrl] = useState(null);  // For local preview
  const [capturedBlob, setCapturedBlob] = useState(null);

  const [status, setStatus] = useState("");
  const videoRef = useRef(null);

  // On mount, try to request camera access
  useEffect(() => {
    async function initCamera() {
      try {
        const s = await navigator.mediaDevices.getUserMedia({ video: true });
        setStream(s);
        setHasCamera(true);
        setCameraError("");
      } catch (err) {
        console.error("Camera init error:", err);
        setHasCamera(false);
        setCameraError("Camera not accessible. Please upload a file instead.");
      }
    }
    if (navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
      initCamera();
    } else {
      setCameraError("getUserMedia not supported in this browser.");
    }
  }, []);

  // Attach stream to videoRef
  useEffect(() => {
    if (videoRef.current && stream) {
      videoRef.current.srcObject = stream;
    }
    return () => {
      // Cleanup: stop camera if unmount
      if (stream) {
        stream.getTracks().forEach((track) => track.stop());
      }
    };
  }, [stream]);

  // Capture from webcam
  const handleCapture = () => {
    if (!videoRef.current) return;
    const video = videoRef.current;
    const width = video.videoWidth;
    const height = video.videoHeight;
    const canvas = document.createElement("canvas");
    canvas.width = width;
    canvas.height = height;
    const ctx = canvas.getContext("2d");
    ctx.drawImage(video, 0, 0, width, height);
    canvas.toBlob((blob) => {
      if (blob) {
        setCapturedBlob(blob);
        const url = URL.createObjectURL(blob);
        setPreviewUrl(url);
      }
    }, "image/jpeg", 0.8);
  };

  // Handle file upload
  const handleFileChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      setCapturedBlob(file);
      const url = URL.createObjectURL(file);
      setPreviewUrl(url);
    }
  };

  // Submit to enroll
  const handleEnroll = async () => {
    if (!studentName) {
      setStatus("Please enter a student name.");
      return;
    }
    if (!capturedBlob) {
      setStatus("Please capture or upload an image first.");
      return;
    }
    try {
      // enrollStudent is your API call to the backend
      const res = await enrollStudent(studentName, capturedBlob);
      setStatus(res.message || "Enrollment successful!");
      // Clear fields
      setStudentName("");
      setCapturedBlob(null);
      setPreviewUrl(null);
    } catch (error) {
      console.error(error);
      setStatus("Enrollment failed.");
    }
  };

  return (
    <div style={{ marginTop: "20px" }}>
      <h2>Enroll New Student</h2>
      <div>
        <label>
          Student Name:{" "}
          <input
            type="text"
            value={studentName}
            onChange={(e) => setStudentName(e.target.value)}
          />
        </label>
      </div>

      {/* If we have camera access, show a webcam preview + "Take Photo" button */}
      {hasCamera ? (
        <div style={{ marginTop: "10px" }}>
          <div
            style={{
              width: "320px",
              height: "240px",
              background: "#000",
              marginBottom: "10px",
            }}
          >
            <video
              ref={videoRef}
              autoPlay
              playsInline
              style={{ width: "100%", height: "auto" }}
            />
          </div>
          <button onClick={handleCapture}>Take Photo</button>
        </div>
      ) : (
        <p style={{ color: "red", marginTop: "10px" }}>{cameraError}</p>
      )}

      {/* File upload fallback */}
      <div style={{ marginTop: "10px" }}>
        <label>
          Or Upload Image:{" "}
          <input type="file" accept="image/*" onChange={handleFileChange} />
        </label>
      </div>

      {/* Preview final selected image */}
      {previewUrl && (
        <div style={{ marginTop: "10px" }}>
          <p>Preview:</p>
          <img
            src={previewUrl}
            alt="preview"
            style={{ maxWidth: "320px", maxHeight: "240px" }}
          />
        </div>
      )}

      {/* Enroll button */}
      <div style={{ marginTop: "10px" }}>
        <button onClick={handleEnroll}>Enroll Student</button>
      </div>

      <p style={{ color: "blue", marginTop: "10px" }}>{status}</p>
    </div>
  );
}

export default Enrollment;
